.skillsBlock {
    min-height: 70vh;
    background-color: darkslategray;
}

.skillsContainer {
    align-items: center;
    flex-direction: column;
    justify-content: flex-start;
}

.title{
    background-color: cadetblue;
}

.skills {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

}

@media screen and (max-width: 749px) {
    .skills{
        justify-content: center;
    }
}